var that;
var langs = [{
	key: "zh-CN",
	text: "中文简体"
}, {
	key: "zh-Tw",
	text: "中文繁体"
}, {
	key: "en-US",
	text: "English"
}];

var langKeys;
var langText;
export default {
	data() {
		return {
			username: '',
			password: '',
			versionTop: '',
			langText: '中文简体',
			langKey: "",
		}
	},
	computed: {
		res() {
			return this.$t('res.login');
		}
	},
	onLoad: function() {
		that = this;
		langKeys = langs.map(function(l) {
			return l.key;
		});
		langText = langs.map(function(l) {
			return l.text;
		});
	},
	onReady: function() {

	},
	methods: {
		Login: function() {
			var uName = that.username.trim();
			var pwd = that.password.trim();
			//console.log(that.res.placPassword)
			if (uName.length == 0) {
				uni.showToast({
					title: "用户名必须填写",
					icon: "none"
				})
				return;
			}

			if (pwd.length == 0) {
				uni.showToast({
					title: "密码必须填写",
					icon: "none"
				})
				return;
			}

			if (uName == "555" && pwd == "123") {
				//this.$store.commit("login", uName);
				uni.redirectTo({
					url: "/pages/index/index"
				})
			} else {
				uni.showToast({
					title: "用户名或者密码错误",
					icon: "none"
				})
				return;
			}
		},
		//切换语言
		ChangeLange: function() {
			uni.showActionSheet({
				itemList: langText,
				success: function(res) {
					that.langText = langText[res.tapIndex];
					that.langKeys = langKeys[res.tapIndex];
					that.$i18n.locale = that.langKeys;
				}
			});
		},
	}
}
